import React, { useState } from "react";
import styled from "styled-components";
import { projects } from "../data";

const Container = styled.section`
  height: 100vh;
  scroll-snap-align: start;
  /* background-color: #272b36; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
`;

const Title = styled.h1`
  color: #fff;
`;

const CarouselWrapper = styled.div`
  width: 80%;
  margin: 2em auto;
  padding: 0 30%;
  display: flex;
  transition: transform ease;
  overflow: hidden;
  flex-direction: row;

  @media (max-width: 768px) {
    flex-direction: column;
    padding: 0;
    align-items: center; /* Adjust this property to align cards based on your preference */
    height: 50%;
  }
`;

const Card = styled.div`

  flex: 0 0 calc(25% - 20px); /* Adjust the width and margin as needed */
  max-width: 100%; /* Ensure the card doesn't exceed the carousel width */
  height: 300px;
  background-color: #bec5cc;
  margin: 0 10px; /* Adjust the margin as needed */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.25em;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 26px;
  transition: all 0.3s;
  cursor: pointer;
  @media (max-width: 768px) {
    flex-direction: column;
    padding: 40px;
    margin: 10px;
    width: 50%;
    height: 200vh;
  }
`;

const Button = styled.button`
z-index: -1;
  margin: 10px;
  --color: #cbd1d7;
  --color2: rgb(10, 25, 30);
  padding: 0.8em 1.75em;
  background-color: transparent;
  border-radius: 6px;
  border: 0.3px solid var(--color);
  transition: 0.5s;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  z-index: 1;
  font-weight: 300;
  font-size: 17px;
  font-family: "Roboto", "Segoe UI", sans-serif;
  text-transform: uppercase;
  color: var(--color);
`;

const Projects = () => {
  // const projects = ["Lamp", "TheCave", "PodReco", "RaptorsxBR"];

  const [currentIndex, setCurrentIndex] = useState(0);

  const visibleProjects = projects.slice(currentIndex, currentIndex + 2);

  const nextSlide = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex + 1 < projects.length ? prevIndex + 1 : 0
    );
  };

  const prevSlide = () => {
    setCurrentIndex((prevIndex) =>
      prevIndex - 1 >= 0 ? prevIndex - 1 : projects.length - 1
    );
  };

  const handleOpenLink = (link) => {
    // Open the link in a new tab or window
    window.open(link, "_blank");
  };

  const [hoveredProject, setHoveredProject] = useState(null);

  const handleMouseEnter = (projectId) => {
    setHoveredProject(projectId);
  };

  const handleMouseLeave = () => {
    setHoveredProject(null);
  };

  return (
    <Container id="projects">
      <Title>Projects</Title>
      <CarouselWrapper>
        {visibleProjects.map((project, index) => (
          <Card
            key={index}
            onClick={() => handleOpenLink(project.link)}
            onMouseEnter={() => handleMouseEnter(project.id)}
            onMouseLeave={handleMouseLeave}
          >
            {" "}
            <br></br>
            {hoveredProject === project.id ? (
              <p style={{textAlign:'center'}}>{project.desc}</p>
            ) : (
              <p >{project.title}</p>
                
            )}
          </Card>
        ))}
      </CarouselWrapper>
      <div>
        <Button onClick={prevSlide}>Prev</Button>
        <Button onClick={nextSlide}>Next</Button>
      </div>
    </Container>
  );
};

export default Projects;
